import BuyCoinPopupWindow from '@/components/app/BuyCoinPopupWindow.vue';
import localizeFilter from "@/filters/localize.filter";
import { thisExpression, throwStatement } from '@babel/types'

export default defineComponent({
    setup() {
        
    },
    components: {
        MainSection,
        // DOMBurningSection,
        ExchangeBurningSection,
        InfoSection,
        LiquidityProvidingPlateSection,
        PagerSection,
        QuestionsSection,
        AboutUsSection,
        PartnersSection,
        ExchangeDirectionsSection,
        LandingFooter,
        RoadmapSection,
        FlowchartSection,
        WhitepapersSection,
        BuyCoinPopupWindow
    },
    data() {
        return {
             showDomBurnViews: false,
             showExchangeBurnViews: false,
             showPopup: false,
             isAndroid:false
        }
    },
    watch: {
        // domBurnTimeLeft(newVal, oldVal) {
        //    this.$nextTick(() => {
        //       this.showDomBurnViews = (newVal > 0);
        //    });
        // },
        exchangeBurnTimeLeft(newVal, oldVal) {
           this.$nextTick(() => {
              this.showExchangeBurnViews = (newVal > 0);
           });
        },
    },
    mounted() {
        // this.showDomBurnViews = (this.domBurnTimeLeft > 0)
        this.showExchangeBurnViews = (this.exchangeBurnTimeLeft > 0)
        if (typeof window.android !== 'undefined'){
            if(typeof window.android.fromAndroid==='function'){
                this.isAndroid=window.android.fromAndroid()
            }
        }
    },
    computed: {
        // domBurnTimeLeft(){
        //      return this.$store.state.info.domBurnTimeLeft
        // },
        exchangeBurnTimeLeft(){
             return this.$store.state.info.exchangeBurnTimeLeft
        },
        popupCoin() {
            return this.$store.getters.popupCoin
        },
    },
    methods: {
        async buyCoin(coinTicker) {
            if (this.$store.getters.isLoggined === true) {
                // await this.$store.dispatch(setBuyCoinTickerToProfile, {coinTicker})
                const {scope, email} = await this.$store.dispatch('getProfile')
                if (scope === 'not_activated_client') {
                    this.$router.push('/confirmEmail?email='+email)
                } else {
                    this.$router.push(`/cabinet?buyCoinTicker=${coinTicker}`)
                }
            } else {
                this.$router.push(`/login?buyCoinTicker=${coinTicker}`)
            }
        },
        register() {
            this.$router.push('/register')
        },
        buyCuprumToken() {
            // this.$router.push('/cabinet/closedAuction?coin=CUT')
            // // console.log('buy')
            window.open('https://t.me/obmenMaks', '_blank')
        },
    }
})
</script>

