const router = createRouter({
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        el: to.hash,
      }
    }
  },
  mode: 'history',
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  if(to?.query?.locale) {
    if(store?.state?.locale?.locale !== to.query.locale) {
      store.commit('SET_LOCALE', to.query.locale)
    }
  }

  const session = localStorage.exchangeClientSession
  const timestamp = Number(localStorage.exchangeClientSessionExpireTimestamp) || null
  let sessionIsExpired = false
  if (timestamp) {
    sessionIsExpired = Date.now() > timestamp
  }
  to.hash = ''
  document.title = to.meta.title ? to.meta.title : "Capital Whale"

  if (to.meta.requiresAuth) {
    if ((session == null || session == undefined) || sessionIsExpired) {
      next({ name: 'Login' })
    } else { 
      next()
    }
  } else
    if (to.name == 'Login' && (session != null && session != undefined && !sessionIsExpired)) {
      next({ name: 'Cabinet' })
    } else {
      next()
    }
})

export default router
