export const listenUnread = (userId, roomId, ts, callback) => {
	console.log(userId, roomId)
	return onSnapshot(unreadQuery(roomId, ts), result => {
		const messages = result.docs.map(msg => msg.data()).filter(({sender_id}) => {
			console.log(sender_id, userId)
			return sender_id != userId
		})
		callback({[roomId]: messages})
	})
}

export const getUnread = async userId => {
	let
		user = await getDoc(userRef(userId)),
		count = 0,
		rooms = {},
		{ lastMessages = {} } = user.data()
	;

	console.log(lastMessages)

	await Promise.all(Object.entries(lastMessages).map(async ([roomId, ts]) => {
		console.log(roomId, ts)
		const result = await getDocs(unreadQuery(roomId, ts))
		console.log(result)
		const messages = result.docs.map(msg => msg.data()).filter(({sender_id}) => sender_id !== userId)
		if(messages.length > 0) {
			rooms[roomId] = messages
			count += messages.length
		}
	}))

	return [ rooms, count ];
}


// ROOMS
const roomsRef = collection(firestoreDb, ROOMS_PATH)

const roomRef = roomId => {
	return doc(firestoreDb, ROOMS_PATH, roomId)
}

export const roomsQuery = (currentUserId, roomsPerPage, lastRoom) => {
	console.log('roomsQuery', currentUserId, roomsPerPage, lastRoom)
	if (lastRoom) {
		return query(
			roomsRef,
			where(USERS_PATH, 'array-contains', currentUserId),
			orderBy(LAST_UPDATED_FIELD, 'desc'),
			limit(roomsPerPage),
			startAfter(lastRoom)
		)
	} else {
		return query(
			roomsRef,
			where(USERS_PATH, 'array-contains', currentUserId),
			orderBy(LAST_UPDATED_FIELD, 'desc'),
			limit(roomsPerPage)
		)
	}
}

