<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal buy-crypto-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            </div>
            <div class="modal-content">
                <div class="title">{{$localizeFilter('choose_payment_title')}}</div>
                <div class="type method" v-if="type==='method'">
                    <div class="cell-container">
                    <div @click="type='fiat'" class="cell" :style="cellStyle">
                        <img class="cell-image" src="@/assets/newImages/fiat.png">
                        <div class="cell-text">{{$localizeFilter('choose_payment_fiat')}}</div>
                    </div>
                    </div>
                    <div class="cell-container">
                    <div @click="type='crypto'" class="cell" :style="cellStyle">
                        <img class="cell-image" src="@/assets/newImages/crypto.png">
                        <div class="cell-text">{{$localizeFilter('choose_payment_crypto')}}</div>
                    </div>
                    </div>
                </div>
                <div class="type fiat" v-if="type==='fiat'">
                    <div class="cell-container" v-for="(method, idx) in paymentMethods.fiat" :key="idx" >
                    <div class="cell" :style="cellStyle" @click="paymentMethodSelected(method)">
                        <img class="cell-image" :src="require(`@/assets/newImages/${method}.png`)">
                        <div class="cell-text">{{$localizeFilter(`choose_payment_${$filters.lowercase(method)}`)}}</div>
                    </div>
                    </div>
                </div>
                <div class="type crypto" v-if="type==='crypto'">
                    <div class="cell-container" v-for="(method, idx) in paymentMethods.crypto" :key="idx">
                    <div class="cell" :style="cellStyle" @click="paymentMethodSelected(method)" :class="(method === 'USDT-bonus' && notEnoughUSDTBonuses) ? 'inactive': ''">
                        <img class="cell-image" :src="require(`@/assets/newImages/${((method === 'USDT' || method === 'USDT-bonus') ? 'USDT-20' : method)}.png`)">
                        <div class="cell-text">{{$localizeFilter(`choose_payment_${$filters.lowercase(method)}`)}}</div>
                    </div>
                    <label class="not-enough-funds-label" v-if="method==='USDT-bonus' && notEnoughUSDTBonuses">{{$localizeFilter('not_enough_funds')}}</label>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        typeOverride: {
            type: String,
            default: null
        },
        paymentMethodsOverride: {
            type: Object,
            default: ()=>null
        },
        cellSizeOverride: {
            type: Object,
            default: ()=>null
        }
    },
    data: ()=>({
        type: 'method', // 'method', 'fiat', 'crypto'
    }),
    computed: {
        paymentMethods() {
            return this.paymentMethodsOverride || this.$store.getters.paymentMethods
        },
        notEnoughUSDTBonuses() {
            return this.$store.getters.notEnoughUSDTBonuses
        },
        cellStyle() {
            if (this.cellSizeOverride) {
                return `width: ${this.cellSizeOverride.width}px; height:  ${this.cellSizeOverride.height}px;`
            } else {
                return ''
            }
        }
    },
    methods: {
        paymentMethodSelected(paymentMethod) {
            this.$emit('paymentMethodSelected', { paymentType: this.type, paymentMethod })
        },
        closeModal() {
            this.$emit('closeModal')
        }
    },
    mounted() {
        this.$nextTick(()=>{
            if (this.typeOverride) {
                this.type = this.typeOverride
            }
        })
    }
})
</script>

