<template>
<div class="sidebar landing-sidebar" :class="{open: value}">
  <div class="top-block">
    <div class="sidebar-logo" @click="openMainPage()">
          <img class="logo-image" src="@/assets/newImages/logo.png">
          <!-- <p class="logo-text">{{$localizeFilter('landing_main_section_title')}}</p> -->
    </div>
    <button class="cross" @click="closeSidebar"></button>
  </div>
  <ul class="sidebar-list">
    <li v-for="(link,index) in links1" class="sidebar-list-item" v-bind:key="index">
        <router-link 
        class="sidebar-button"
        active-class="active"
        tag="button"
        :to="link.url"
        :exact="link.exact"
        @click="handleTap(link.url)"
        >{{ link.title }}</router-link>
    </li>
    <div class="coins-dropdown-block">
      <div class="place-for-trigger-button">
          <button id="sidebar-coins-dropdown-trigger" class="transparent-button coins-dropdown-trigger" :class="showCoinsDropdown ? 'selected' : ''" @click="triggerCoinsDropdown()">{{$localizeFilter("coins")}}</button>
      </div>
      <CoinsDropdown :style="'margin-left:'+coinsDropdowMargin+'px;'" class="coins-dropdown" v-if="showCoinsDropdown" @closeCoinsDropdown="closeCoinsDropdown"/>
    </div>
    <li v-for="(link,index) in links2" class="sidebar-list-item" v-bind:key="index">
        <router-link 
        class="sidebar-button"
        active-class="active"
        tag="button"
        :id="link.id || null"
        :to="link.url"
        :exact="link.exact"
        @click="handleTap(link.url)"
        >{{ link.title }}</router-link>
    </li>
  </ul>
 
  <div class="dropdown-block">
      <LangsDropdownTrigger @triggerDropdown="triggerDropdown"/>
      <div class="coins-dropdown-block">
        <LangsDropdown class="dropdown" v-if="showDropdown" @closeDropdown="closeDropdown"/>
      </div>
  </div>

</div>
</template>

<script>
import localizeFilter from "@/filters/localize.filter";
import { defineComponent } from "@vue/runtime-core";
import LangsDropdownTrigger from "@/components/app/langs/LangsDropdownTrigger.vue"
import LangsDropdown from "@/components/app/langs/LangsDropdown.vue"
import CoinsDropdown from "@/components/landing/app/CoinsDropdown.vue"

export default defineComponent({
  setup() {
    // // console.log(this.data.links);
  },
  components: {
    LangsDropdownTrigger, LangsDropdown, CoinsDropdown
  },
  props: ["value"],
  data: function() {
    return {
      links1: [
        { name: 'home', title: localizeFilter("home"), url: "/#home", exact: true },
        { name: 'liquidityProviding', title: localizeFilter("landing_liquidity_providing_title"), url: "/liquidity-providing", selected: false },
        { name: 'articlesAboutUs', title: localizeFilter("articles_about_us"), url: "/articlesAboutUs", selected: false },
        { name: 'rate', title: localizeFilter("rate"), url: "/rate", selected: false },
        // { name: 'news', title: localizeFilter("news"), url: "/news", selected: false },
      ],
      links2: [
        // { title: "", url: "/plans" },
        // { title: localizeFilter('marketing'), url: "/marketing" },
        // { title: "", url: "#" },
        { name: 'aboutUs', title: localizeFilter('about_us'), url: "/#aboutUs" },
        { name: 'contacts', title: localizeFilter('contacts'), url: "/#contacts" },
        { title: localizeFilter('login'), url: "/login" },
        { title: localizeFilter('registration'), url: "/register" },
      ],
      showDropdown: false, 
      showCoinsDropdown: false
    };
  },
  watch: {
    $route(to, from) {
          let lastChar = to.fullPath.substr(to.fullPath.length - 1);
          if (lastChar != '#') {
            this.$emit('closeSidebar')
          }
    }
  },
  computed: {
      coinsDropdowMargin() {
      const element = document.getElementById('sidebar-coins-dropdown-trigger')
      // // console.log(element)
      if (element) {
        const xPos = element.getBoundingClientRect().x
        // // console.log(xPos)
        return xPos
      } else {
        return 0
      }
    },
  },
  methods: {
    openMainPage() {
      this.$router.push('/')
    },
    handleTap: function(hashbang) {
      if (hashbang == "#") {
        this.triggerCoinsDropdown()
      } else {
        this.scrollFix(hashbang)
      }
    },
    scrollFix: function(hashbang) {
        const idx = hashbang.lastIndexOf('#')
        const name = hashbang.slice(idx + 1);
        if (idx === -1 || name === '' || name.includes('/')) {
          window.scrollTo({top: 0, behavior: 'auto'})
          return
        }
        const element = document.getElementById(name)
        if (element) {
          const y = element.getBoundingClientRect().top + window.pageYOffset;
          window.scrollTo({top: y-50, behavior: 'auto'});
        }
    },
    triggerDropdown() {
      this.showDropdown = !this.showDropdown
    },
    closeDropdown() {
      this.showDropdown = false
    },
    triggerCoinsDropdown() {
      this.showCoinsDropdown = !this.showCoinsDropdown
    },
    closeCoinsDropdown() {
      this.showCoinsDropdown = false
    },
    closeSidebar() {
      this.$emit('closeSidebar')
    }
  }
});
</script>

