import store from '../store'
import dateFormat from '@/utils/dateFormat'

export default function dateTimeFilter(value, format = 'date', formatString = null, options = null) {
  let resultOptions = {}
  if (format.includes('custom')) {
    if (formatString) {
      return (new Date(value)).format(formatString)
    }
    if (options) {
      resultOptions = options
    } 
  } else {
    if (format.includes('date')) {
      resultOptions.day = '2-digit'
      resultOptions.month = 'long'
      resultOptions.year = 'numeric'
    }
    if (format.includes('time')) {
      resultOptions.hour = '2-digit'
      resultOptions.minute = '2-digit'
    }
  }
  const locale = store.state.locale
  return new Intl.DateTimeFormat(locale , resultOptions).format(new Date(value))
}