<template>
  <div id="app">
    <component :is="layout">
      <router-view />
    </component>
  </div>
</template>

<script>
import { defineComponent } from "@vue/composition-api";
import EmptyLayout from "@/layouts/EmptyLayout";
import MainLayout from "@/layouts/MainLayout";
import ExchangeLayout from "@/layouts/ExchangeLayout";
import LandingLayout from "@/layouts/LandingLayout";
import AdditionalLandingLayout from "@/layouts/AdditionalLandingLayout"
import ChatLayout from "@/layouts/ChatLayout"
import M from "materialize-css";
import { setupInterceptors } from './utils/httpInterceptors';
import * as firestoreService from '@/database/firestore'

export default defineComponent({
  setup() {
  },
  data: ()=>({ 
    // interval: null
    unsubscribe: null
  }),
  mounted() {
    M.AutoInit();
    // window.addEventListener("logout", (event) => {
    //     console.log(window)
    //     console.log('LOGOUT')
    //     this.logout()
    // }, false);

    // this.interval = setInterval(()=>{
    //   this.$userActionMessage(1, 'register')
    //   this.$userActionMessage(2, 'increaseLevel')
    //   this.$userActionMessage(3, 'buy', 100.2342, 'GDC')
    // }, 2000)
  },
  watch: {
    profile(newVal, oldVal) {
      if(newVal !== null && typeof newVal !== 'undefined') {
        if (this.$store.getters.isLoggined == true) {
            this.$nextTick(()=>{
              const id = newVal?.id
              if (id !== null && typeof id !== 'undefined') {
                this.subscribe(id)
              }
            })
        } else {
          if (this.unsubscribe) {
            this.$nextTick(()=>{
              this.unsubscribe()
              this.unsubscribe = null
            })
          }
        }
      }
    }
  },
  unmounted() {
    // clearInterval(this.interval)
    if (this.unsubscribe) {
      this.unsubscribe()
      this.unsubscribe = null
    }
  },
  methods: {
    async subscribe(id) {
      this.unsubscribe = await firestoreService.subscribeForUpdates(id)
    }
    // async logout() {
    //   try {
    //     await this.$store.dispatch('logout')
    //     this.$router.push('/login')
    //   } catch (e) {
    //    // this.$error(e)
    //     this.$router.push('/login')
    //   }
    // },
  },
  created() {
    setupInterceptors(this.$store)
  },
  components: {
    EmptyLayout,
    MainLayout,
    ExchangeLayout,
    LandingLayout,
    AdditionalLandingLayout,
    ChatLayout
  },
  computed: {
    profile() {
			return this.$store.getters.profile
		},
    layout() {
      // // console.log(this.$route.meta.layout)
      return (this.$route.meta.layout || "empty") + "-layout";
    },
  },
  // unmounted() {
  //   window.removeEventListener("logout")
  //   // clearInterval(this.interval)
  //   // this.interval = null
  // }
});
</script>

