<script>
import { defineComponent } from '@vue/composition-api'
import YouTube from 'vue3-youtube'

export default defineComponent({
    setup() {
        
    },
    components: { YouTube },
    data: () => ({
        email: '???@gmail.com',
        socialNetworksLinks: {
            youtube: {
                'ru-RU': 'https://www.youtube.com/channel/UC7to7Nai9043raVVvKYm6Hg',
                'en-US': 'https://www.youtube.com/channel/UCu0EOJRjdgjW2TCF9g8klZA',
                'es-ES': 'https://www.youtube.com/channel/UCIGIAyJo6XukNb2IYRobtfw',
            },
            vk:'https://vk.com/capitalwhale1',
            // twitter:'https://twitter.com/CapitalWhaleOff',
            yandexZen:'https://zen.yandex.ru/capitalwhale',
            facebook:'https://www.facebook.com/people/Capital-Whale/100080972809910/',
            telegramChannel: 'https://t.me/Capital_Whale',
            telegramChat:'https://t.me/+FYZCWBFFhuswYmM6',
        },
        showDomBurnViews: false,
        mountDate: +Date.now()
        // telegram: '@CapitalWhaleOfficial/telegram',
        // telegramLink: https://t.me/+FYZCWBFFhuswYmM6',
        // emailLink: '#'
    }),
    watch: {
        domBurnTimeLeft(newVal, oldVal) {
            this.$nextTick(() => {
                this.showDomBurnViews = (newVal > 0);
            });
        }
    },
    computed: {
        showZen() {
            return this.locale === 'ru-RU'
        },
        showVK() {
            return this.locale === 'ru-RU'
        },
        videoLink() {
            if (this.locale === 'ru-RU') {
                return 'https://www.youtube.com/embed/FsT_Ftk2sEo'
            } else {
                return 'https://www.youtube.com/embed/10JdHbBp_qw'
            }
        },
        locale() {
            return this.$store.getters.selectedLocale
        },
        domBurnTimeLeft(){
             return this.$store.state.info.domBurnTimeLeft
        },
        frameWidth() {
            return Math.min(Math.min(window.innerWidth, window.screen.availWidth) - 40, 450);
        },
        frameHeight() {
            return this.frameWidth * 0.4444;
        },
        videoId() {
            return `landingFooterVideo-${this.mountDate}`
        }
    },
    methods: {
        onPlayerReady() {
            // // console.log('player ready')
        },
        onPlayerStateChange(event) {
            if (event.data === 2) { // video paused
                this.$emit('showBuyCoinPopup')
            }
        },
        downloadApp() {
            window.open('https://cwcto.github.io/CapitalWhale.apk', '_blank')
        }
    },
    mounted() {
        this.showDomBurnViews = (this.domBurnTimeLeft > 0)
    }
})
</script>

