  <div class="top-block">
    <div class="sidebar-logo" @click="openMainPage()">
          <img class="logo-image" src="@/assets/newImages/logo.png">
          <!-- <p class="logo-text">{{'Logo text'}}</p> -->
    </div>
    <button class="cross" @click="closeSidebar"></button>
  </div>

    <div class="user-info-block">
      <p class="user-info">{{user ? (user?.firstName + ' ' + user?.lastName) : ''}}</p>
      <p class="user-info">ID: {{user?.id}}</p>
    </div>

   <div class="dropdown-block">
      <LangsDropdownTrigger @triggerDropdown="triggerDropdown"/>
      <div class="place-for-dropdown">
        <LangsDropdown class="dropdown" :classOverride="'gray'" v-if="showDropdown" @closeDropdown="closeDropdown" @forceUpdateAll="forceUpdateAll"/>
      </div>
  </div>

  <transition name="collapse-fade">
