  <transition name="collapse-fade">
  <div class="dom-burn-block" v-if="showDomBurnViews">
    <div class="dom-burn-top-block">
        <img class="dom-coin" src="@/assets/newImages/coins/DOM.png">
        <div class="dom-burn-texts-block">
            <div class="dom-burn-text text" v-html="$localizeFilter('dom_coin_burning')"> </div>
            <div class="dom-burn-text time">{{$utils.formatTime(domBurnTimeLeft)}}
                <img class="fire" src="@/assets/newImages/fire.png">
            </div>
        </div>
    </div>
    <div class="dom-burn-bottom-block">
      <a :href="$store.state.info.domDetailsLink"  target="_blank">
        <button class="basic-button">{{$localizeFilter('details_1')}}</button>
      </a>
      <a :href="$store.state.info.domBuyLink"  target="_blank">
        <button class="basic-button buy-dom-button">
            <label class="buy-dom-label">{{$localizeFilter('buy') + '!'}}</label>
            <img class="buy-dom-image" src="@/assets/newImages/fire.png">
          </button>
      </a>
    </div>
   </div>
  </transition>

  <ul class="sidebar-list">
    <li v-for="(link,index) in links" class="sidebar-list-item" :class="link.class ?? 'default' + (link.active ? ' active' : ' inactive')" v-bind:key="index">
        <button
        class="sidebar-button"
        :class="(link.class ?? 'default') + (isCurrentLocation(link.url) ? ' selected':'') + (link.active ? ' active' : ' inactive')"
        @click.native="openUrl(link.url)"
        >
        <img class="sidebar-button-image" :class="link.class ?? 'default'" :src="link.image">
        <label class="sidebar-button-label" :class="link.class ?? 'default'" :style="getLinkStyle(link)">{{ link.title }}</label></button>
        <label class="new-label" v-if="link.isNew == true">{{'new!'}}</label>
        <label class="messeger-label" v-if="link.isMessenger == true && newMessagesCount > 0">{{newMessagesCount}}</label>
        <label class="soon-label" v-if="link.isSoon==true">{{$localizeFilter('soon')}}</label>
        <img src="@/assets/newImages/sidebar/active_tab_mark.png" class="selected-mark" :class="(isCurrentLocation(link.url) ? 'selected':'')">
    </li>
  </ul>
  <div class="bottom-block">
    <button class="logout-button" @click="logout()">{{$localizeFilter('logout_from_account')}}</button>
  </div>
</div>
</template>

<script>
import localizeFilter from "@/filters/localize.filter";
import { defineComponent } from "@vue/runtime-core";
import LangsDropdownTrigger from "@/components/app/langs/LangsDropdownTrigger.vue"
import LangsDropdown from "@/components/app/langs/LangsDropdown.vue"

let allowedExchange = new Set([
  2, 49018, 1529, 22934 //, 4
])

export default defineComponent({
  setup() {
    // // console.log(this.data.links);
  },
  components: {
    LangsDropdownTrigger, LangsDropdown
  },
  props: ["value"],
  computed: {
      newMessagesCount() {
        return this.$store.getters.newMessagesCount
      },
      user() {
        return this.$store.state.info.profile
      },
      domBurnTimeLeft(){
          return this.$store.state.info.domBurnTimeLeft
      },
      isDemo() {
        return this.$store.getters.isDemo
      },
      allowed() {
          return allowedExchange.has(+localStorage.exchangeClientId)
      },
      links() {
          return [
            { name: 'wallet', title: localizeFilter(this.isDemo ? 'sidebar_wallet_demo' : 'sidebar_wallet'), url: `/cabinet/wallet?demo=${Number(this.isDemo)}`, image: require('@/assets/newImages/sidebar/sidebar_wallet.png'), exact: true, active: true, isNew: false, isSoon: false},
            { name: 'bonusAccountGDC', title: localizeFilter('bonus_account_coin', ['GDC']), url: '/cabinet/bonusAccount?coin=GDC', image: require('@/assets/newImages/sidebar/sidebar_coins.png'), exact: true, active: true, isNew: false},
            { name: 'bonusAccountUSDT', title: localizeFilter('bonus_account_coin', ['USDT']), url: '/cabinet/bonusAccount?coin=USDT', image: require('@/assets/newImages/sidebar/sidebar_coins.png'), exact: true, active: true, isNew: false},
            { name: 'bonusAccountDRMC', title: localizeFilter('bonus_account_coin_2', ['DRMC']), url: '/cabinet/bonusAccount?coin=DRMC', image: require('@/assets/newImages/sidebar/sidebar_coins.png'), exact: true, active: true, isNew: false},
            { name: 'bonusAccountCWPT', title: localizeFilter('bonus_account_coin_2', ['CWPT']), url: '/cabinet/bonusAccount?coin=CWPT', image: require('@/assets/newImages/sidebar/sidebar_coins.png'), exact: true, active: true, isNew: true},
            { name: 'bonusAccountFENIX', title: localizeFilter('bonus_account_coin_2', ['FENIX']), url: '/cabinet/bonusAccount?coin=FENIX', image: require('@/assets/newImages/sidebar/sidebar_coins.png'), exact: true, active: true, isNew: true},

            { name: 'promotions', title: localizeFilter('promotions'), url: '/cabinet/promotions', image: require('@/assets/newImages/sidebar/sidebar_promotions.png'), exact: true, active: true, isNew: false, color: "#FF9400"},
            { name: 'coins', title: localizeFilter('coins'), url: '/cabinet/coins' /*url: ''*/, image: require('@/assets/newImages/sidebar/sidebar_coins2.png'), active: true, isNew: false},
            { name: 'instruments', title: localizeFilter('sidebar_instruments'), url: '', image: '', class: 'list-header', active: true, isNew: false},
            { name: 'exchange', title: localizeFilter(this.isDemo ? 'sidebar_exchange_demo' : 'sidebar_exchange'), /*url: '/cabinet/exchange'*/ url: `/cabinet/exchange?demo=${Number(this.isDemo)}&firstCoin=BTC&lastCoin=USDT`, image: require('@/assets/newImages/sidebar/sidebar_sigma.png'), class: 'list-item', active: true, isNew: true, isSoon: false},
            { name: 'analytics', title: localizeFilter('sidebar_analytics'), url: '/cabinet/analytics' /*url: ''*/, image: require('@/assets/newImages/sidebar/sidebar_analytics.png'), class: 'list-item', active: true, isNew: false},
            { name: 'closedAuction', title: localizeFilter('closed_auction'), url: '/cabinet/closedAuction', image: require('@/assets/newImages/sidebar/sidebar_closed_auction.png'), class: 'list-item', active: true, isNew: (JSON.parse(localStorage.exchangeClosedAuctionPageOpened ?? null) != true)},
            { name: 'trading', title: localizeFilter('trading'), url: '', image: require('@/assets/newImages/sidebar/sidebar_trading.png'), class: 'list-item', active: false, isNew: false},
            { name: 'arbitration', title: localizeFilter('arbitration'), url: '/cabinet/arbitration', image: require('@/assets/newImages/sidebar/arbitration.png'), class: 'list-item', active: this.allowed, isNew: false, isSoon: true},
            { name: 'staking', title: localizeFilter('staking'), url: '/cabinet/staking', image: require('@/assets/newImages/sidebar/sidebar_staking.png'), class: 'list-item', active: true, isNew: true},

            { name: 'liquidityProvider', title: localizeFilter('liquidity_provider'), url: '/cabinet/liquidityProvider', image: require('@/assets/newImages/sidebar/liquidity_provider.png'), class: 'list-item', active: this.user?.testBalance > 0, isNew: false},
            { name: 'goals', title: localizeFilter('goals'), url: '/cabinet/goals', image: require('@/assets/newImages/sidebar/liquidity_provider.png'), class: 'list-item', active: true, isNew: false},
            { name: 'technologies', title: localizeFilter('technologies'), url: '', image: require('@/assets/newImages/sidebar/sidebar_technologies.png'), class: 'list-item', active: false, isNew: false},
            { name: 'startups', title: localizeFilter('startups'), url: '', image: require('@/assets/newImages/sidebar/sidebar_startups.png'), class: 'list-item', active: false, isNew: false},
            { name: 'vacancies', title: localizeFilter('vacancies'), url: '', image: require('@/assets/newImages/sidebar/sidebar_vacancies.png'), class: 'list-item', active: false, isNew: false},
            { name: 'messenger', title: localizeFilter('messenger'), url: '/cabinet/messenger', image: require('@/assets/newImages/sidebar/sidebar_messenger.png'), active: true, isNew: false, isMessenger:true },
            { name: 'partners', title: localizeFilter('sidebar_partners'), url: '/cabinet/partners', image: require('@/assets/newImages/sidebar/sidebar_partners.png'), active: true, isNew: false },
            { name: 'referralProgram', title: localizeFilter('referral_program'), url: '/cabinet/referralProgram', image: require('@/assets/newImages/sidebar/sidebar_refferal_program.png'), active: true, isNew: false },
            { name: 'trafficArbitration', title: localizeFilter('traffic_arbitration'), url: '/cabinet/trafficArbitration', image: require('@/assets/newImages/sidebar/sidebar_traffic_arbitration.png'), active: true, isNew: false, color: '#28BEB2'},
            // { name: 'developmentProgress', title: localizeFilter('development_progress'), url: '/cabinet/developmentProgress', image: require('@/assets/newImages/sidebar/sidebar_trading.png'), active: true, isNew: false},
            { name: 'profile', title: localizeFilter('sidebar_profile'), url: '/cabinet/profile', image: require('@/assets/newImages/sidebar/sidebar_profile.png'), active: true, isNew: false},
            { name: 'help', title: localizeFilter('sidebar_help'), url: '/cabinet/help', image: require('@/assets/newImages/sidebar/sidebar_help.png'), active: true, isNew: false},
            { name: 'aboutUs', title: localizeFilter('sidebar_about_us'), url: '/cabinet/aboutUs', image: require('@/assets/newImages/sidebar/sidebar_about_us.png'), active: true, isNew: false},
            
        ]
      }
  },
  data: function() {
    return {
      showDomBurnViews: false,
      name: '',
      id: '',
      showDropdown: false
    };
  },
  watch: {
    $route(to, from) {
      this.$emit('closeSidebar')
      this.update()
    },
    domBurnTimeLeft(newVal, oldVal) {
        this.$nextTick(() => {
          this.showDomBurnViews = (newVal > 0);
        });
    }
  },
  methods: {
    getLinkStyle(link) {
      let result = ''
      if (link.name == 'referralProgram') {
         result = result + 'font-size: 22px!important;'
      }
      if (link.color) {
       result = result + `color: ${link.color}!important;`
      }
      return result
    },
    openMainPage() {
      this.$router.push('/')
    },
    openUrl(url) {
      this.$router.push(url)
    },
    triggerDropdown() {
      this.showDropdown = !this.showDropdown
    },
    closeDropdown() {
      this.showDropdown = false
    },
    closeSidebar() {
      this.$emit('closeSidebar')
    },
    update() {
      this.$forceUpdate();
    },
    isCurrentLocation(url) {
      return (url === location.href.split(location.host)[1].replace('#/',''))
    },
    async logout() {
      try {
        await this.$store.dispatch('logout')
        this.$router.push('/')
      } catch (e) {
       // this.$error(e)
        this.$router.push('/')
      }
    },
    forceUpdateAll() {
      this.$emit('forceUpdateAll')
    }
  },
  async mounted() {
      this.showDomBurnViews = (this.domBurnTimeLeft > 0)
      window.addEventListener('exchangeClosedAuctionPageOpened', (event) => {
          this.links.forEach((link) => {
            if (link.name == 'closedAuction') {
              link.isNew = false
              this.forceUpdateAll()
            }
          })
         
      });
    },
    unmounted() {
      // window.removeEventListener('exchangeClosedAuctionPageOpened')
    }
});
</script>

