  .section-title {
    font-size: 36px;
    line-height: 42px;
  }

  .section-text {
    font-size: 20px;
    line-height: 23px;
  }

  .app-content {
    margin-left: 400px;
  }

  .app-content.full {
    margin-left: 0;
  }

  .sidebar,  .sidebar.open  {
      width:400px;
      -webkit-transform:translateX(0);
      transform:translateX(0);
  }

  .cross {
    margin-left: 0;
    visibility: hidden;
    width: 0;
    height: 0;
  }

  .link-to-sidebar {
    visibility: hidden;
  }

  .navbar {
    visibility: hidden;
    height: 0!important;
  }
}

@media(max-width: 1100px) {
  .section-title {
    font-size: 28px;
    line-height: 36px;
  }

  .section-text {
    font-size: 16px;
    line-height: 19px;
  }

  .app-content {
    padding-left:0;
  }

  .sidebar{
      width:400px;
      -webkit-transform:translateX(-400px);
      transform:translateX(-400px);
  }

  .sidebar.open {
    -webkit-transform:translateX(0);
    transform:translateX(0);
  }

  .cross {
    margin-left: 20px;
    visibility: visible;
  }

  .link-to-sidebar {
    visibility: visible;
  }

  .navbar {
    visibility: visible;
  }
  .app-content {
    margin-top: 80px;
  }
}

</style>
