<template>
    <div class="buy-coin-popup-window" :id="windowId" :class="`${view} ${position}`" :style="windowStyle">
        <div v-if="(view === 'bigCoinLight') || (view === 'bigCoinDark')">
            <img :src="require('@/assets/newImages/coins/' + coin.name + '.png')" class="big-coin">
            <div class="main-block" :class="view">
                <div class="coin-button-block">
                        <router-link  v-if="isRouterLink" :to="buyCoinLink" target="_blank">
                            <button class="buy-button" :class="view" @click="$emit('buyCoin', coin.ticker)">
                                <span style="text-shadow: 4px 7px 15px rgba(0, 0, 0, 0.29);">{{$localizeFilter('buy')}}</span>
                            </button>
                        </router-link>
                        <a v-else :href="buyCoinLink" target="_blank">
                            <button class="buy-button" :class="view"  @click="$emit('buyCoin', coin.ticker)">
                                <span style="text-shadow: 4px 7px 15px rgba(0, 0, 0, 0.29);">{{$localizeFilter('buy')}}</span>
                            </button>
                        </a>
                </div>
                <div class="text">{{$localizeFilter('buy_button_popup_text_big_coin', [coin.name])}}</div>
                <button class="orange-cross" @click="$emit('close')"></button>
                <!-- <img class="bottom-triangle" :src="require('@/assets/newImages/popup_triangle.png')"> -->
            </div>
        </div>

        <div class="main-block" :class="view" v-else>
            <div class="text">{{$localizeFilter('buy_button_popup_text_coin')}}</div>
            <div class="coin-button-block">
                    <img :src="require('@/assets/newImages/coins/icons/' + coin.ticker.toLowerCase() + '_icon.png')" class="coin-icon">
                    <router-link  v-if="isRouterLink" :to="buyCoinLink" target="_blank">
                        <button class="buy-button"  @click="$emit('buyCoin', coin.ticker)">
                            <span style="text-shadow: 4px 7px 15px rgba(0, 0, 0, 0.29);">{{$localizeFilter('buy')}}</span>
                        </button>
                    </router-link>
                    <a v-else :href="buyCoinLink" target="_blank">
                        <button class="buy-button"  @click="$emit('buyCoin', coin.ticker)">
                            <span style="text-shadow: 4px 7px 15px rgba(0, 0, 0, 0.29);">{{$localizeFilter('buy')}}</span>
                        </button>
                    </a>
            </div>
            <img class="bottom-triangle" :src="require('@/assets/newImages/popup_triangle.png')">
        </div>

    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        coin: {
            type: Object,
            required: true
        },
        buyCoinLink: {
            type: String,
            default: null
        },
        isRouterLink: {
            type: Boolean,
            default: false
        },
        view: {
            type: String,
            default: 'coin' // coin, bigCoinLight, bigCoinDark
        },
        position: {
            type: String,
            default: 'bottomLeft' // bottomLeft, center
        },
    },
    data: ()=> ({
        mountDate: +new Date(),
        windowStyle: ''
    }),
    computed: {
        windowId() {
            return `buyCoinPopupWindow-${this.mountDate}`
        }
    },
    setup() {
        
    },
    mounted() {
        this.$nextTick(()=>{
            const el = document.getElementById(this.windowId)
            if (el) {
                 const windowWidth = el.offsetWidth
                 const windowHeight = el.offsetHeight
                 this.windowStyle = ((this.position === 'center') ? `margin-left: -${windowWidth/2.0}px; margin-top: -${windowHeight/2.0}px;` : '')
            }
           
        })
    }
})
</script>

