<template>
    <div class="flowchart-container">
        <div class="flowchart">
            <div class="flowchart-line" v-for="(line, idx_line) in lines" v-bind:key="idx_line">
                <!-- <div v-resize-text="{ratio:1.5}" class="flowchart-block" v-for="(item, idx) in line.items" v-bind:key="idx">{{ item.text }}</div> -->
                <div class="flowchart-block" v-for="(item, idx) in line.items" v-bind:key="idx">{{ item.text }}</div>
            </div>
        </div>

    </div>
    
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '../../filters/localize.filter'

export default defineComponent({
    data: ()=>({
        items: [
            {
                text: localizeFilter('landing_cuprum_industry_binding_1')
            },
            {
                text: localizeFilter('landing_cuprum_industry_binding_2')
            },
            {
                text: localizeFilter('landing_cuprum_industry_binding_3')
            },
            {
                text: localizeFilter('landing_cuprum_industry_binding_4')
            },
            {
                text: localizeFilter('landing_cuprum_industry_binding_5')
            },
            {
                text: localizeFilter('landing_cuprum_industry_binding_6')
            },
            {
                text: localizeFilter('landing_cuprum_industry_binding_7')
            },
            {
                text: localizeFilter('landing_cuprum_industry_binding_8')
            },
            
            
        ],
        itemsInRow: 1
    }),
    setup() {

    },
    computed: {
        lines(){
            let res = [];
            let currLayer=-1;
            for(let i=0; i<this.items.length; i++){
                if(i%this.itemsInRow==0) {
                    res.push({
                        items: []
                    });
                    currLayer++;
                }
                res[currLayer].items.push(this.items[i]);
            }
            return res;
        },
        locale(){
            return this.$store.getters.selectedLocale;
        }
    },

    // async loadData(){
        
    // },
    mounted(){
        let anchor = this.$el;
        let totalWidth = anchor.offsetWidth;
        let itemsInRow = 1;
        while (341*(itemsInRow+1)+74*(itemsInRow)<=totalWidth) itemsInRow++;
        this.itemsInRow = itemsInRow;

        if(this.locale === 'id-ID') {
            var styles = `
                .flowchart-block:last-child {
                   font-size: 18px;
                }
            `
            var styleSheet = document.createElement("style")
            styleSheet.innerText = styles
            document.head.appendChild(styleSheet)
        }


        // loadData();
    }
})
</script>

