// exchange timer
const timestamp = 1665511200000
// const timestamp = +new Date() + 10000
store.dispatch('runExchangeBurnTimer', {timestamp: timestamp})

document.addEventListener('visibilitychange', function() {
  if(document.hidden) {
      // tab is now inactive
      // temporarily clear timer using clearInterval() / clearTimeout()
      store.dispatch('stopExchangeBurnTimer')
  }
  else {
      // tab is active again
      // restart timers
      store.dispatch('runExchangeBurnTimer', {timestamp: timestamp})
  }
});

