<template>
    <section class="main-section">
    <Particles
    id="tsparticles"
    v-if="showParticles"
    :options="particlesOptions"
    />
    <div class="section-container">
        <div class = "content-block" id="mainSectionContent">
                <div class = "title">{{title}}</div>
                <div class = "text" id="mainSectionText">{{text}}</div>
                <button class="button basic-button" style="z-index: 1" @click="register">{{$localizeFilter('join')}}</button>
            </div>
        </div>
    </section>
</template>

<script>
import Globe from 'globe.gl';
import { defineComponent } from '@vue/composition-api'
import { ref } from "vue";

export default defineComponent({
    props: {
        title: String,
        text: String
    },
    data: () => ({
        showParticles: true,
        
        particlesOptions: {
        fullScreen: { enable: false },
        // background: {
        //   color: {
        //     value: '#0d47a1',
        //   },
        // },
        fpsLimit: 60,
        interactivity: {
          detectsOn: 'window',
          events: {
            onClick: {
              enable: true,
              mode: 'push',
            },
            onHover: {
              enable: true,
              mode: 'repulse',
            },
            resize: true,
          },
          modes: {
            bubble: {
              distance: 400,
              duration: 2,
              opacity: 0.8,
              size: 40,
              speed: 3,
            },
            push: {
              quantity: 4,
            },
            repulse: {
              distance: 200,
              duration: 0.4,
            },
          },
        },
        particles: {
          color: {
            value: '#ffffff',
          },
          links: {
            color: '#ffffff',
            distance: 150,
            enable: true,
            opacity: 0.5,
            width: 1,
          },
          collisions: {
            enable: false,
          },
          move: {
            direction: 'none',
            enable: true,
            outMode: 'bounce',
            random: false,
            speed: 4,
            straight: false,
            // bounce: false
          },
          number: {
            density: {
              enable: true,
              value_area: 800,
            },
            value: 80,
            limit: 120,
          },
          opacity: {
            value: 0.5,
          },
          shape: {
            type: 'circle',
          },
          size: {
            random: true,
            value: 5,
          },
        },
        detectRetina: true,
      }
    }),
    setup() {
        
    },
    methods: {
        register() {
            this.$router.push('/register')
        },
        insertAfter(newNode, existingNode) {
            existingNode.parentNode.insertBefore(newNode, existingNode.nextSibling);
        },
    },
    computed: {
    },
    mounted() {
        const N = 25;
        const arcsData = [...Array(N).keys()].map(() => ({
        startLat: (Math.random() - 0.5) * 180,
        startLng: (Math.random() - 0.5) * 360,
        endLat: (Math.random() - 0.5) * 180,
        endLng: (Math.random() - 0.5) * 360,
        // color: [['red', 'white', 'blue', 'green'][Math.round(Math.random() * 3)], ['red', 'white', 'blue', 'green'][Math.round(Math.random() * 3)]]
        }));

        const M = 300;
        const gData = [...Array(M).keys()].map(() => ({
        lat: (Math.random() - 0.5) * 180,
        lng: (Math.random() - 0.5) * 360,
        size: Math.random() / 3
        // color: ['red', 'white', 'blue', 'green'][Math.round(Math.random() * 3)]
        }));

        const myGlobe = Globe({showGlobe: true, showAtmosphere: true, showGraticules: true});
        const e = document.createElement('div')

        let globe;
        e.id = "globe"
        fetch('https://cwcto.github.io/datasets/ne_110m_admin_0_countries.geojson').then(res => res.json()).then(countries => {
          globe.hexPolygonsData(countries.features)
            .hexPolygonResolution(3)
            .hexPolygonMargin(0.3)
            .hexPolygonColor(() => '#20958B') //'#145e58') //'#791cfc') //`#${Math.round(Math.random() * Math.pow(2, 24)).toString(16).padStart(6, '0')}`)
        }); 
        

        globe = myGlobe(e)
            .globeImageUrl('https://cwcto.github.io/textures/earth-blue-dark.jpg')
            .backgroundColor('rgba(0,0,255,0)')
            .showGlobe(true)
            .showAtmosphere(true)
            .arcsData(arcsData)
            .arcColor(()=> '#ADFFF6') //'#ffffff') //'#e7eb1a') //'#20958B') //'#791cfc')
            // .arcDashLength(() => Math.random())
            .arcStroke(0.4)
            .arcDashGap(() => Math.random())
            .arcDashAnimateTime(() => Math.random() * 10000 + 500)
            
            .atmosphereColor('#a2defc')
            .atmosphereAltitude(0.3)
            // .pointsData(gData)
            // .pointAltitude('size')
            // .pointColor(()=>'#7bbefe')
            // .pointRadius(0.1)
            // .pointsTransitionDuration(10000);
        
        globe.controls().autoRotate = true;
        globe.controls().autoRotateSpeed = 1;
            
            // .hexPolygonsData([[0, -90], [0, 90], [180, 90], [180, -90], [0, -90]])
            // .height(500)
            // .width(200)
          const e2 = document.getElementById('mainSectionText')
          this.insertAfter(e, e2)
        
        // myGlobe.pointOfView({'lat': c[position].lat, 'lng':c[position].lng, 'altitude':c[position].alt }, 2000);

        // const e3 = document.getElementById('tsparticles')
        // // console.log(e3)
    }
});
</script>

