import filters from '@/filters/filters'
import localizeFilter from '@/filters/localize.filter'
import messagePlugin from '@/plugins/message.plugin'



export default {
    // format time for timers
    formatTime(time, withDays = false, withHours = true) {
        let totalTime = Math.floor((time)/1000);
        if (totalTime <= 0) {
            return '00:00'
        }
        if (withDays) {
            let days = Math.floor(totalTime / 86400)
            let otherTime = totalTime % 86400
            let hours = Math.floor(otherTime / 3600)
            otherTime = otherTime % 3600
            let minutes = Math.floor(otherTime / 60)
            let seconds = Math.floor(otherTime % 60)
            return `${days > 0 ? (days + ':') : ('')}${hours >= 10 ? hours : ('0' + hours)}:${minutes >= 10 ? minutes : ('0' + minutes)}:${seconds >= 10 ? seconds : ('0' + seconds)}`;
        } else if (withHours) {
            let hours = Math.floor(totalTime / 3600)
            let otherTime = totalTime % 3600
            let minutes = Math.floor(otherTime / 60)
            let seconds = Math.floor(otherTime % 60)
            return `${hours}:${minutes >= 10 ? minutes : ('0' + minutes)}:${seconds >= 10 ? seconds : ('0' + seconds)}`;
        } else {
            let hours = Math.floor(totalTime / 3600)
            let otherTime = totalTime % 3600
            let minutes = Math.floor(otherTime / 60)
            let seconds = Math.floor(otherTime % 60)
            return `${minutes >= 10 ? minutes : ('0' + minutes)}:${seconds >= 10 ? seconds : ('0' + seconds)}`;
        }
    },
    formatNumber: function(amount, locale = 'en-US', minimumFractionDigits = 0, maximumFractionDigits = 0) {
        var formatter = new Intl.NumberFormat(locale, {
            minimumFractionDigits: minimumFractionDigits,
            maximumFractionDigits: maximumFractionDigits,
          }); 
          return formatter.format(amount);
    },
    formatCurrency: function(amount, currency, locale = 'en-US', minimumFractionDigits = 0, maximumFractionDigits = 0) {
        var formatter = new Intl.NumberFormat(locale, {
            style: 'currency',
            currency: 'USD',
            minimumFractionDigits: minimumFractionDigits, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
            maximumFractionDigits: maximumFractionDigits, // (causes 2500.99 to be printed as $2,501)
          }); 
          return formatter.format(amount); /* $2,500.00 */
    },
    addMonths: function(date, months) {
        var d = date.getDate();
        date.setMonth(date.getMonth() + +months);
        if (date.getDate() != d) {
          date.setDate(0);
        }
        return date;
    },
    getCoinTicker: function(coinName) {
        switch (coinName) {
            case 'DOM':
                return 'DOM'
            case 'GadgetCoin':
                return 'GDC'
            case 'CapitalWhalePresaleToken':
                return 'CWPT'
            case 'InternetCoin':
                return 'ITC'
            default:
                return null
        }
    },
    getAllIndexes: function(str, val) {
        var indexes = [];
        for(var i=0; i<str.length;i++) {
            if (str[i] === val) indexes.push(i);
        }
        return indexes
    },
    getLangCodeFromLocale (locale, uppercase = true) {
        if (locale === 'en-US') { return uppercase ? 'EN' : 'en' }
        if (locale === 'uk-UA') { return uppercase ? 'UK' : 'uk' }
        if (uppercase == true) {
            return filters.capitalize(locale.substring(0,2))
        } else {
            return filters.lowercase(locale.substring(0,2))
        }
    },
    getAlpha2FromLocale (locale, uppercase = true) {
        if (uppercase == true) {
            return filters.capitalize(locale.substring(3,5))
        } else {
            return filters.lowercase(locale.substring(3,5))
        }
    },
    displayBigIntAsDecimal(value, decimalsNumber, leaveDigitsAfterDot) {
        let sign = 1;
        if(value < 0 || value[0] == '-'){
            sign = -1;
            value = -value;
        }
        const decimals = decimalsNumber.toString().length - 1;
        if(!leaveDigitsAfterDot) leaveDigitsAfterDot = decimals;
        if(decimals >= value.toString().length) {
          return (sign==-1 ? '-': '') + '0.' + ('0'.repeat(decimals - value.toString().length) + value.toString()).slice(0, leaveDigitsAfterDot);
        }
        return value.toString().slice(0, -decimals) + '.' + value.toString().slice(-decimals).slice(0, leaveDigitsAfterDot);
    },
    copy(value) {
        function fallbackCopyTextToClipboard(text) {
            var textArea = document.createElement("textarea");
            textArea.value = text;
            
            // Avoid scrolling to bottom
            textArea.style.top = "0";
            textArea.style.left = "0";
            textArea.style.position = "fixed";

            document.body.appendChild(textArea);
            textArea.focus();
            textArea.select();
            var successful;
            try {
                successful = document.execCommand('copy');
                var msg = successful ? 'successful' : 'unsuccessful';
                // console.log('Fallback: Copying text command was ' + msg);
            } catch (err) {
                console.error('Fallback: Oops, unable to copy', err);
            }

            document.body.removeChild(textArea);
            return successful;
        }
        if (value) {
            const clipboardData =
            // event.clipboardData ||
            window.clipboardData ||
            // event.originalEvent?.clipboardData ||
            navigator.clipboard;
            try {
                clipboardData.writeText(value); 
                messagePlugin.message(localizeFilter('copied')) 
            } catch (e) {
                if(fallbackCopyTextToClipboard(value)){
                    messagePlugin.message(localizeFilter('copied')) 
                }
            }
        }
  }
}