import M from 'materialize-css'
import localizeFilter from '@/filters/localize.filter'
import utils from '@/utils/utils.js'

let message = function(html) {
    M.toast({html})
}

let error = function(error) {
    let html = ''
    if (error.code) {
        html = localizeFilter(error.code)
    } else {
        html = localizeFilter('error_message',[error.message])
    }
    M.toast({html})
}

// USER ACTION MESSAGES

let pendingUserActionMessages = []
let lastUserActionMessageDisplayTimestamp = 0
let userActionMessageDisplayTimeout = 2000
let maxPendingMessagesCount = 20
let maxDisplayedUserActionMessagsCount = 5

let userActionMessage = function(userId, action, coin, amount, messageIsPending = false) {
    let timestamp = Date.now()
    
    // // check if message is already in pending queue
    // const message = {userId, action, coin, amount}
    // let messageIsPending = pendingUserActionMessages.includes(message)

    // check if there are too many displayed messages
    let elements = document.querySelectorAll('.toast.user-action-message')
    let displayedMessages = Array.prototype.slice.call(elements)

    if (displayedMessages.length >= maxDisplayedUserActionMessagsCount) {
        // if message is in pending queue, just not display it, the message will be displayed later
        if (messageIsPending) { return }
        // else {
        //     addMessageToPendingQueue(userId, action, coin, amount)
        //     return
        // }
    }

    // if limit of displayed messages not reached, check the timeout
   // if (timestamp - lastUserActionMessageDisplayTimestamp > userActionMessageDisplayTimeout) {
        if (messageIsPending) { 
            displayUserActionMessage(userId, action, coin, amount, timestamp)
            pendingUserActionMessages.shift()
        } else {
            // if (pendingUserActionMessages.length > 0) { 
                addMessageToPendingQueue(userId, action, coin, amount)
            // } else {
            //     displayUserActionMessage(userId, action, coin, amount, timestamp)
            // }
        }
    // } else {
    //     // if timeout not reached, add message to pending queue
    //     addMessageToPendingQueue(userId, action, coin, amount)
    // }
}

let addMessageToPendingQueue = function(userId, action, coin, amount) {
    pendingUserActionMessages.push({userId, action, coin, amount})
    // left only last maxPendingMessagesCount messages
    if (pendingUserActionMessages.length > maxPendingMessagesCount) {
        pendingUserActionMessages = pendingUserActionMessages.slice(-maxPendingMessagesCount)
    }
}

let displayUserActionMessage = function(userId, action, coin, amount, timestamp) {
    let toastHTML = null
    lastUserActionMessageDisplayTimestamp = timestamp
    // let toastID = `userActionToast${timestamp}`

    if (action === 'register') {
        toastHTML = `${localizeFilter('user_registered', [userId])}`;
    } else if (action === 'increaseLevel') {
        toastHTML = `${localizeFilter('user_increased_level', [userId])}`;
    } else if (action === 'coinPurchase') {
        const amountString = utils.formatNumber(amount, 'en-US', 0, 2)
        toastHTML = `${localizeFilter('user_bought', [userId, amountString, coin])}`;
    } else if (action === 'addMonetizationChannel') {
        const amountString = utils.formatNumber(amount, 'en-US', 0, 2)
        toastHTML = `${localizeFilter('user_added_monetization_channel', [userId])}`;
    }
    if (toastHTML) { M.toast({html: toastHTML, classes: 'user-action-message', displayLength: 10000}) } // id: toastID
}

// display pending messages every userActionMessageDisplayTimeout ms
let interval = setInterval(() => {
    if (pendingUserActionMessages.length > 0) { 
        let message = pendingUserActionMessages[0]
        userActionMessage(message.userId, message.action, message.coin, message.amount, true)
    }
}, userActionMessageDisplayTimeout);

// clearInterval(interval)

export default {
    install: (app, options) => {
        app.config.globalProperties.$message = message
        app.config.globalProperties.$error = error
        app.config.globalProperties.$userActionMessage = userActionMessage
    },

    message: message,
    error: error,
    userActionMessage: userActionMessage
}
