<template>
<div class="whitepapers-list-container">
    <button class="arrow-button left-arrow" :disabled="currentPage===1">
        <img class="arrow-image" src="@/assets/newImages/arrow_pager_left.png" @click="turnLeft">
    </button>
    <div class="whitepapers-list-block">
        <div class="whitepapers-list" :id="listId">
            <div class="whitepapers-page">
                <div v-for="j in itemsPerCurrentPage" :key="j" class="whitepaper-block">
                <Whitepaper 
                :style="`width: ${pageWidth}px;`"
                :coinTicker="whitepapers[(currentPage-1)*itemsPerPage+j-1].coinTicker"
                :whitepaperLink="whitepapers[(currentPage-1)*itemsPerPage+j-1].whitepaperLink"
                :buyLink="whitepapers[(currentPage-1)*itemsPerPage+j-1].buyLink"
                />
                </div>
            </div>
        </div>
        <div class="page-indicator-block">
            <div v-for="i in pagesCount" :key="i" class="page-indicator" :class="{'active':(currentPage===i)}">
        </div>
        </div>
    </div>
    <button class="arrow-button right-arrow" @click="turnRight" :disabled="currentPage===pagesCount">
        <img class="arrow-image" src="@/assets/newImages/arrow_pager_right.png">
    </button>
</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import Whitepaper from './Whitepaper'
export default defineComponent({
    data: ()=>({
        whitepapers: [
        {coinTicker: 'CWPT', whitepaperLink: null, buyLink: 'https://t.me/obmenMaks'},
        {coinTicker: 'CUT', whitepaperLink: null, buyLink: 'https://t.me/obmenMaks'},
        {coinTicker: 'ITC', whitepaperLink: null, buyLink: 'https://t.me/obmenMaks'},
        {coinTicker: 'GDC', whitepaperLink: null, buyLink: 'https://t.me/obmenMaks'},
        // {coinTicker: 'GDC', whitepaperLink: null, buyLink: 'https://www.google.com/'},
        // {coinTicker: 'GDC', whitepaperLink: null, buyLink: 'https://www.google.com/'},
        // {coinTicker: 'GDC', whitepaperLink: null, buyLink: 'https://www.google.com/'},
        // {coinTicker: 'DOM', whitepaperLink: null, buyLink: 'https://www.google.com/'},
        // {coinTicker: 'DOM', whitepaperLink: null, buyLink: 'https://www.google.com/'},
        // {coinTicker: 'DOM', whitepaperLink: null, buyLink: 'https://www.google.com/'},
        // {coinTicker: 'GDC', whitepaperLink: null, buyLink: 'https://www.google.com/'},
        // {coinTicker: 'DOM', whitepaperLink: null, buyLink: 'https://www.google.com/'}
        ],
        currentPage: 1,
        mountDate: +Date.now(),
        pageWidth: Math.min(window.innerWidth-110, 350),
    }),
    components: {
        Whitepaper
    },
    computed: {
        itemsPerPage() {
            const windowWidth = window.innerWidth
            const availableWidth = Math.min(windowWidth ,1200) 
            return Math.floor(availableWidth / this.pageWidth)
        },
        itemsPerCurrentPage() {
            if (this.currentPage === this.pagesCount) {
                if (this.whitepapers.length % this.itemsPerPage === 0) {
                    return this.itemsPerPage
                } else {
                    return this.whitepapers.length % this.itemsPerPage
                }
            } else {
                return this.itemsPerPage
            }
        },
        pagesCount() {
            return Math.ceil((this.whitepapers.length) / this.itemsPerPage)
        },
        listId() {
            return `whitepapersList-${this.mountDate}`
        }
    },
    methods: {
        turnLeft() {
            this.currentPage = Math.max(this.currentPage-1, 1)
        },
        turnRight() {
            this.currentPage = Math.min(this.currentPage+1, this.pagesCount)
        },
        // handleScroll() {
        //     const el = document.getElementById(this.listId)
        //     if (el) {
        //          const pageWidth = el.offsetWidth
        //          this.currentPage = Math.floor(el.scrollLeft / pageWidth) + 1
        //     }
        // }
    },
    setup() {
        
    },
    mounted() {
        // const el = document.getElementById(this.listId)
        // if (el) {
        //     el.addEventListener('scroll', this.handleScroll)
        // }
    },
    unmounted() {
        // const el = document.getElementById(this.listId)
        // if (el) {
        //     el.addEventListener('scroll', this.handleScroll)
        // }
    },
    created() {
        
    }
})
</script>

