.pager {
  width:100%;
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}

.page {
  position: relative;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media(min-width: 768px) {
  .pager {
    align-content: center;
    justify-content: space-around;
    gap: 20px;
  }
  .page {
      width: 350px;
      height: 300px;
      margin-top:20px;
  }
}

@media(max-width: 768px) {
  .pager {
    align-content: space-around;
    justify-content: center;
    gap: 16px;
  }

  .page {
      width: 100%;
      width: -webkit-fill-available;
      width: -moz-available;
      width: stretch;
  }
}

</style>
