<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal buy-crypto-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button thin" @click="closeModal"></button>
            </div>
        <div class="modal-content">
            <div class="top-block">
                <img :src="require('@/assets/newImages/coins/' + coin.name + '.png')" class="coin-image">
                <p class="window-text mobile" :style="'margin-left: 10px;'">{{$localizeFilter('buy_crypto_modal_2_text', [coin.name])}}</p>
            </div>
            
            <div class="text-amount-block">
                <p class="window-text desktop">{{$localizeFilter('buy_crypto_modal_2_text', [coin.name])}}</p>
                <div class="amount-block">
                    <p class="window-text">{{$localizeFilter('amount_of', [coin.ticker])}}:</p>
                    <div class="input-field">
                        <input id="amount" class="custom-input-2" :placeholder="$localizeFilter('amount_of', [coin.ticker])" type="text" v-model.trim="amount" :class="{ invalid: v$.amount.$error }"/>
                        <small class="error" v-for="(error, index) of v$.amount.$errors" :key="index">
                            {{ error.$message }}
                        </small>
                    </div>
                </div>
            </div>

            <label v-if="coin.canBuyToBonusBalance" class="text">{{ $localizeFilter('  ') + ':\n' }}</label>

            <Toggle v-if="coin.canBuyToBonusBalance" :class="'toggle-block'" :title="''" :variants="views" @toggle="toggle"/>

            <div class="address-block" v-if="isCryptoWallet">
                <p class="window-text">{{$localizeFilter('tron_wallet_address')}}:</p>
                <div class="input-field">
                    <input id="walletAddress" class="custom-input-2" :placeholder="$localizeFilter('wallet_address')" type="text" v-model.trim="walletAddress" :class="{ invalid: v$.walletAddress.$error }" v-maska="'TXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX'"/>
                    <small class="error" v-for="(error, index) of v$.walletAddress.$errors" :key="index">
                        {{ error.$message }}
                    </small>
                </div>
            </div>

            <button class="buy-button submit-button" :class="submitInProgress ? 'inactive' : ''" @click="submitHandler">{{$localizeFilter('buy')}}</button>
        </div>
    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { helpers, required, requiredIf, numeric, minValue, maxValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'
import Toggle from "@/components/Toggle.vue"

export default defineComponent({
   components: {
    Toggle,
  },
   props: {
        coin: {
            type: Object,
            required: true
        },
        paymentMaxAmount: {
            type: Number,
            required: false
        },
    },
    validations() {
        return {
            amount: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
                numeric: helpers.withMessage(localizeFilter('field_must_be_numeric'), numeric),
                minValue: helpers.withMessage(localizeFilter('min_value', [this.minAmount]), minValue(this.minAmount)),
                maxValue: helpers.withMessage(localizeFilter('max_value', [this.paymentMaxAmount]), maxValue(this.paymentMaxAmount || Infinity)),
            },
            walletAddress: {
                required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), requiredIf(function(){
                    return this.isCryptoWallet;
                })),
                minLength: helpers.withMessage(localizeFilter('min_length', [34]), minLength(34)),
                maxLength: helpers.withMessage(localizeFilter('min_length', [34]), maxLength(34)),
            },
        };
    },
    setup() {
        return { v$: useVuelidate() };
    },
  data: ()=>({
        amount: 0,
        minAmount: 0,
        views: ['crypto_wallet', 'bonus_account'],
        isCryptoWallet: true,
        walletAddress: null,
        submitInProgress: false,
        // rate: null,
        // availableAmount: null
  }),
  methods: {
    closeModal(reload) {
        this.$emit('closeModal', reload)
    },
    toggle(value) {
        this.isCryptoWallet = (this.views[value] === 'crypto_wallet')
    },
    async submitHandler() {
        this.v$.$touch();
        if (this.v$.$error) {
            return;
        }
        var WAValidator = require('wallet-address-validator');
        if(this.isCryptoWallet && !WAValidator.validate(this.walletAddress, 'trx')){
            this.$error({message: localizeFilter('incorrect_wallet_address')})
            return
        }
    //  this.submitInProgress = true
        this.$emit('choosePaymentMethod', {amount: Number(this.amount), walletAddress: this.isCryptoWallet ? this.walletAddress : null})
    //  setTimeout(()=>{
    //      this.submitInProgress = false
    //   },1000)
    }
  }
})
</script>


