<template>
    <div>
    <transition-group name="fade" mode="in-out">
        <ChoosePaymentMethodModal v-if="showChoosePaymentMethodModal" @paymentMethodSelected="paymentMethodSelected" @closeModal="closeModals"/>

        <TransferModal class="transfer-modal" v-if="showTransferModal" :object="$localizeFilter('level') + ' ' + paymentLevel" :amount="paymentLevelData.amount" :transferData="paymentLevelData.transferData" @closeModal="closeModals"/>

        <InfoModal class="continue-payment-modal" v-if="showContinuePaymentModal" :view="'full'" :text="continuePaymentLevelModalText" :button="{title: $localizeFilter('continue'), action: continuePayment}" @closeModal="closeModals"/>

        <PaymentSuccessfulModal class="payment-successful-modal" v-if="showPaymentSuccessfulModal" :view="'center'" :text="$localizeFilter('level_payment_successful_alert_text', [paymentLevelData.level])" :button="{title: 'Ok', action: closeModals}"/>

        <div class="darken" v-if="showLoader">
            <Loader v-if="showLoader" visible="true"/>
        </div>
    </transition-group>
    
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import ChoosePaymentMethodModal from '@/components/newModals/ChoosePaymentMethodModal.vue'
import InfoModal from "@/components/newModals/InfoModal.vue"
import PaymentSuccessfulModal from "@/components/newModals/PaymentSuccessfulModal.vue"
import Loader from "@/components/app/Loader.vue"
import localizeFilter from '@/filters/localize.filter'
import utils from '@/utils/utils'
import TransferModal from '../newModals/TransferModal.vue'


export default defineComponent({
    setup() {
        
    },
    components: {
    InfoModal,
    PaymentSuccessfulModal,
    ChoosePaymentMethodModal,
    Loader,
    TransferModal
},
    data: ()=>({
        showChoosePaymentMethodModal: false,
        showTransferModal: false,
        showContinuePaymentModal: false,
        showPaymentSuccessfulModal: false,
        showLoader: false
    }),
    computed: {
        paymentLevelData() {
            return this.$store.getters.paymentLevelData
        },
        continuePaymentLevelModalText() {
            return this.$store.getters.continuePaymentLevelModalText
        },
        paymentLevel() {
            return this.$store.getters.paymentLevel
        }
    },
    //  this.$store.dispatch('setPaymentLevelData', {data})
    methods: {
        async paymentMethodSelected(data) {
            this.showChoosePaymentMethodModal = false
            this.showLoader = true
            try {
                await this.$store.dispatch('setPaymentLevelMethod', {data: data})
                await this.$store.dispatch('createMarketingLevelOrder')
                if (this.paymentLevelData.transferData) {
                     this.$nextTick(()=>{
                       // this.showChoosePaymentMethodModal = false
                        this.showLoader = false
                        this.showTransferModal = true
                    })
                } else if (this.paymentLevelData.cutEquivalentAmount || this.paymentLevelData.operatorTransferData) {
                    this.$nextTick(()=>{
                        // this.showChoosePaymentMethodModal = false
                        this.showLoader = false
                        this.showContinuePaymentModal = true
                        if(this.paymentCoinData.cutEquivalentAmount) {
                            this.needShowLoader = true;
                        }
                    })
                } else {
                    this.$nextTick(()=>{
                        this.showLoader = false
                        window.open(this.paymentLevelData.redirectTo, '_blank').focus()
                        // this.showLoader = true
                    })
                } 
            } catch(e) {
                this.$error(e)
            } 
        },
        continuePayment() {
            this.$nextTick(()=>{
                window.open(this.paymentLevelData.redirectTo, '_blank').focus()
                // this.showContinuePaymentModal = false
                this.showLoader = true
            })
        },
        paymentSuccessful() { // TODO: call this method from redirect
            this.$nextTick(()=>{
                this.showLoader = false
                this.showPaymentSuccessfulModal = true
                this.clearAll()
            })
        },
        closeModals() {
            this.showChoosePaymentMethodModal = false
            this.showContinuePaymentModal = false
            this.showPaymentSuccessfulModal = false
            this.showTransferModal = false
            this.$emit('close')
            this.clearAll()
        },
        async clearAll() {
            await this.$store.dispatch('clearPaymentLevelData')
        }
    },
    async mounted() {
        try {
            await this.$store.dispatch('getPaymentMethods')
            this.$nextTick(()=>{
                this.showChoosePaymentMethodModal = true
            })
        } catch(e) {
            this.$error(e)
        } 
    }
})
</script>

