<template>
    <div>
    <transition-group name="fade" mode="in-out">
        <BuyCryptoModal2 v-if="showBuyCryptoModal" :coin="paymentCoin" :paymentMaxAmount="paymentMaxAmount" @choosePaymentMethod="choosePaymentMethod" @closeModal="closeModals"/>
    
        <ChoosePaymentMethodModal v-if="showChoosePaymentMethodModal" @paymentMethodSelected="paymentMethodSelected" @closeModal="closeModals"/>

        <TransferModal class="transfer-modal" v-if="showTransferModal" :coin="paymentCoin" :amount="paymentCoinData.amount" :transferData="paymentCoinData.transferData" @closeModal="closeModals"/>

        <InfoModal class="continue-payment-modal" v-if="showContinuePaymentModal" :view="'full'" :text="continuePaymentCoinModalText" :button="{title: $localizeFilter('continue'), action: continuePayment}" @closeModal="closeModals"/>

        <PaymentSuccessfulModal class="payment-successful-modal" v-if="showPaymentSuccessfulModal" :view="'center'" :text="$localizeFilter('payment_successful_alert_text', [paymentCoin.ticker])" :button="{title: 'Ok', action: closeModals}"/>

        <div class="darken" v-if="showLoader">
            <Loader v-if="showLoader" visible="true"/>
        </div>
    </transition-group>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import BuyCryptoModal2 from "@/components/newModals/BuyCryptoModal2.vue"
import ChoosePaymentMethodModal from '@/components/newModals/ChoosePaymentMethodModal.vue'
import InfoModal from "@/components/newModals/InfoModal.vue"
import PaymentSuccessfulModal from "@/components/newModals/PaymentSuccessfulModal.vue"
import Loader from "@/components/app/Loader.vue"
import localizeFilter from '@/filters/localize.filter'
import utils from '@/utils/utils'
import TransferModal from '../newModals/TransferModal.vue'


export default defineComponent({
    setup() {
        
    },
    components: {
    BuyCryptoModal2,
    InfoModal,
    PaymentSuccessfulModal,
    ChoosePaymentMethodModal,
    Loader,
    TransferModal
},
    data: ()=>({
        showBuyCryptoModal: false,
        showChoosePaymentMethodModal: false,
        showTransferModal: false,
        showContinuePaymentModal: false,
        showPaymentSuccessfulModal: false,
        showLoader: false,
        needShowLoader: false,
    }),
    computed: {
        paymentCoin() {
            return this.$store.getters.paymentCoin
        },
        paymentMaxAmount() {
            return this.$store.getters.paymentMaxAmount
        },
        paymentCoinData() {
            return this.$store.getters.paymentCoinData
        },
        continuePaymentCoinModalText() {
            return this.$store.getters.continuePaymentCoinModalText
        }
    },
    methods: {
        choosePaymentMethod(data) {
            this.$store.dispatch('setPaymentCoinData', {data})
            this.$nextTick(()=>{
                this.showBuyCryptoModal = false
                this.showChoosePaymentMethodModal = true
            })
        },
        async paymentMethodSelected(data) {
            this.showChoosePaymentMethodModal = false
            this.showLoader = true
            try {
                await this.$store.dispatch('setPaymentCoinMethod', {data: data})
                await this.$store.dispatch('createCoinOrder')
                if (this.paymentCoinData.transferData) {
                    this.$nextTick(()=>{
                        // this.showChoosePaymentMethodModal = false
                        this.showLoader = false
                        this.showTransferModal = true
                    })
                } else if (this.paymentCoinData.cutEquivalentAmount || this.paymentCoinData.operatorTransferData) {
                    this.$nextTick(()=>{
                        // this.showChoosePaymentMethodModal = false
                        this.showLoader = false
                        this.showContinuePaymentModal = true
                        if(this.paymentCoinData.cutEquivalentAmount) {
                            this.needShowLoader = true;
                        }
                    })
                } else {
                    this.$nextTick(()=>{
                        window.open(this.paymentCoinData.redirectTo, '_blank').focus()
                        // this.showLoader = true
                    })
                } 
            } catch(e) {
                this.showLoader = false;
                this.$error(e)
            } 
        },
        continuePayment() {
            this.$nextTick(()=>{
                window.open(this.paymentCoinData.redirectTo, '_blank').focus()
                this.showContinuePaymentModal = false
                if(this.needShowLoader) {
                    this.showLoader = true
                }
            })
        },
        paymentSuccessful() { // TODO: call this method from redirect
            this.$nextTick(()=>{
                this.showLoader = false
                this.showPaymentSuccessfulModal = true
                this.clearAll()
            })
        },
        closeModals() {
            this.showBuyCryptoModal = false
            this.showChoosePaymentMethodModal = false
            this.showContinuePaymentModal = false
            this.showPaymentSuccessfulModal = false
            this.showTransferModal = false
            this.$emit('close')
            this.clearAll()
        },
        async clearAll() {
            await this.$store.dispatch('clearPaymentCoinData')
            const path = this.$router?.currentRoute?.value?.fullPath
            if (path) {
                const newRoute = path.replace(`&buyCoinTicker=${this.paymentCoin.ticker}`, '')
                this.$router.push(newRoute)
            }
        }
    },
    async mounted() {
        try {
            await this.$store.dispatch('getPaymentMethods')
            this.$nextTick(()=>{
                this.showBuyCryptoModal = true
            })
        } catch(e) {
            this.$error(e)
        } 
    }
})
</script>

