<template>
    <div class="dropdown" :class="classOverride">
        <div v-for="locale,idx in locales" v-bind:key="idx" class="dropdown-item" @click.stop="changeLocale(locale)">
            <img :src="require('@/assets/newImages/flags/flag_' + locale +'.png')" class="flag">
            <button class="lang-button" :class="selectedLocale === locale ? 'selected' : ''">{{$localizeFilter('locale_'+locale)}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import {getCurrentInstance} from 'vue'

export default defineComponent({
    props: {
        classOverride: {
            type: String,
            default: 'blue'
        }
    },
    setup() {
        
    },
    computed: {
        locales() {
            return this.$store.getters.locales
        },
        selectedLocale() {
            return this.$store.getters.selectedLocale
        }
    },
    methods: {
        changeLocale(locale) {
            this.$store.commit('SET_LOCALE', locale)
            this.$emit('closeDropdown')
            this.$router.go()
        }
    }
})
</script>

