import globalConfig from '@/globalConfig'

const url = globalConfig.getWSHostName()
let socket = null;
const subscribed = new Set();

function getPromise () {
    let resolve, reject;
    let promise = new Promise((res, rej) => {
        resolve = res;
        reject = rej;
    });
    return { promise, resolve, reject };
}

function connect () {
    if (socket !== null) return;
    socket = new WebSocket(url);

    socket.onopen = () => {
        socket.send(JSON.stringify({
            role: 'client',
            sessionId: localStorage.exchangeClientSession
        }))
    }
    socket.onmessage = (e) => { // recieving message from client
        // console.log(e, subscribed)
        try {
            let message = JSON.parse(e.data)
            // console.log(subscribed)
            for (let cb of subscribed) {
                // console.log(cb)
                cb(message);
            }
        } catch (e) {
            // console.log(e);
        }
    };

    socket.onerror = (e) => {
        // console.log("error", e);
        socket = null;
    };

    socket.onclose = (e) => {
        // console.log("close", e);
        socket = null;
    };
}

setInterval(() => {
    connect();
}, 1000);

const wsApi = {
    isConnected: () => {
        return socket !== null;
    },
    subscribe: (cb) => {
        subscribed.add(cb);
        // console.log(subscribed)
    },
    unsubscribe: (cb) => {
        subscribed.delete(cb);
    },
    sendMessageForClient: async (text, ticketId) => {
        const data = {
            text,
            ticketId
        };
        socket.send(JSON.stringify(data));
        return null;
    },
};

export { wsApi };