<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
   props: {
        coin: {
            type: Object,
            default: null
        },
        object: {
            type: String,
            default: null
        },
        amount: {
            type: Number,
            default: null
        },
        transferData: {
            type: Object,
            required: true
        }
    },
    computed: {
        amountString() {
            return this.transferData.amount + ' ' + this.transferData.coinTicker
        }
    },
    methods: {
        closeModal(reload) {
            this.$emit('closeModal', reload)
        }
    }
})
</script>


