<template>
    <div class="question" @click="(e) => handleClick(e)">
        <div class="title-block">
            <img class="mark" src="@/assets/newImages/blue_circle.png">
             <p class="title"> {{content.title ?? ''}} </p>
        </div>
        <p class="text" v-if="isOpen">
            {{content.text ?? ''}}
        </p>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: [
        'content'
    ],
    data: () => ({
        isOpen: false
    }),
    setup() {
        
    },
    methods: {
        handleClick(e) {
            // // console.log(e)
            e.target.blur()
            this.isOpen = !this.isOpen
        }
    },
})
</script>

