<template>
    <div class="mini-text-block" :style="`border: 1px solid ${color}; border-${roundCorner}-radius: 15px;`">
        <div class="mini-text-block-title" :style="`color: ${color};`">
            {{title}}
        </div>
        <div class="place-for-text">
            <div class="mini-text-block-text">{{text}}</div>
            <button class="mini-text-block-button" v-if="button" :style="styleObject" @click="action">{{button.title}}</button>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: ['color', 'title', 'text', 'roundCorner', 'button'],
    setup() {
        
    },
    methods: {
        action(){
            this.$router.push(this.button.link)
        }
    },
    computed: {
        styleObject() {
            return {
                '--color': this.color+'4D',
                '--color-hover': this.color+'9A',
                '--border-color': this.color
            }
        }
    }
})
</script>

