import store from '../store'
import ru from '../locales/ru.json'
import en from '../locales/en.json'
import uk from '../locales/uk.json'
import es from '../locales/es.json'
import kk from '../locales/kk.json'
import uz from '../locales/uz.json'
import ky from '../locales/ky.json'
import th from '../locales/th.json'
import cn from '../locales/cn.json'
import tr from '../locales/tr.json'
import id from '../locales/id.json'

const locales = {
  'en-US': en,
  'es-ES': es,
  'id-ID': id,
  'kk-KK': kk,
  'ky-KY': ky,
  'ru-RU': ru,
  'th-TH': th,
  'tr-TR': tr,
  'uk-UA': uk,
  'uz-UZ': uz,
  'zh-CN': cn
}

export default function localizeFilter(key, args = []) {
  const locale = store.state.locale
  let result = locales[locale][key] || locales['en-US'][key] || key //|| `[Localize error]: key ${key} not found`
  result = sprintf(result, ...args);
  return result
}

function sprintf(str) {
  var args = arguments, i = 1;

  return str.replace(/%(s|d|0\d+d)/g, function (x, type) {
      var value = args[i++];
      switch (type) {
      case 's': return value;
      case 'd': return parseInt(value, 10);
      default:
          value = String(parseInt(value, 10));
          var n = Number(type.slice(1, -1));
          return '0'.repeat(n).slice(value.length) + value;
      }
  });
}