<template>
    <div class="coins-dropdown">
        <div v-for="coin,idx in coins" v-bind:key="idx" class="coins-dropdown-item" :class="(coin.name=='DOM' && showDomBurnViews) ? 'dom-burning-card' : ''" @click="openCoinPage(coin)" >
            <div class="coin-block">
                <img :src="require('@/assets/newImages/coins/icons/' + coin.ticker.toLowerCase() + '_icon.png')" class="coin-icon">
                <div class="open-coin-page-button" >{{coin.localizedName}}</div>
            </div>
            
             <transition name="fade" mode="out-in">
                <div class="dom-burning" v-if="(coin.name=='DOM') && showDomBurnViews">
                    <div class="dom-burn-text">{{ $localizeFilter('until_burning')}}</div>
                    <div class="dom-burn-time">{{ '' + $utils.formatTime(domBurnTimeLeft)}}</div>
                </div>
             </transition>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import {getCurrentInstance} from 'vue'

export default defineComponent({
    setup() {
        
    },
    data: () => ({
        showDomBurnViews: false
    }),
    watch: {
        domBurnTimeLeft(newVal, oldVal) {
            this.$nextTick(() => {
                this.showDomBurnViews = (newVal > 0);
            });
        }
    },
    computed: {
        coins() {
            // return this.$store.getters.coins//.filter(coin=>coin.ticker!='CUT')
            return this.$store.getters.coins.filter(coin=>coin.ticker!='FENIX')
        },
        domBurnTimeLeft(){
             return this.$store.state.info.domBurnTimeLeft
        },
    },
    mounted() {
        this.showDomBurnViews = (this.domBurnTimeLeft > 0)
    },
    methods: {
        openCoinPage(coin) {
            this.$emit('closeCoinsDropdown')
            this.$router.push('/coins/' + coin.name)
        }
    }
})
</script>

