<template>
    <div class="additional-landing-layout">
    <LandingNavbar class="navbar" @click="isOpen = !isOpen" @triggerDropdown="triggerDropdown" @triggerCoinsDropdown="triggerCoinsDropdown" :coinsDropdownClosed="!showCoinsDropdown"/>
    <LandingSidebar class="sidebar" v-model="isOpen" :value="isOpen" @closeSidebar="closeSidebar"/>
    <!-- <div class="darken" v-if="isOpen" @click="isOpen = !isOpen"></div> -->

      <main class="app-content" :class="{full: true}">
        <div class="app-page">
          <router-view />
          <LandingFooter class="landing-footer" id="contacts"/>
        </div>
      </main>
      <CoinsDropdown class="coins-dropdown" v-bind:style="`top: ${coinsDropdownPosition.y}px; left: ${coinsDropdownPosition.x}px;`" v-if="showCoinsDropdown" @closeCoinsDropdown="closeCoinsDropdown"/>
      <LangsDropdown class="dropdown" v-bind:style="`top: ${dropdownPosition.y}px; left: ${dropdownPosition.x}px;`" v-if="showDropdown" @closeDropdown="closeDropdown" />
      <!-- <UnderMaintenanceModal />  -->
    </div>
</template>

<script>
import LandingNavbar from '@/components/landing/app/LandingNavbar.vue'
import LandingSidebar from "@/components/landing/app/LandingSidebar.vue";
import LangsDropdown from "@/components/app/langs/LangsDropdown.vue"
import CoinsDropdown from "@/components/landing/app/CoinsDropdown.vue"
import { defineComponent } from "@vue/runtime-core";
import LandingFooter from "@/components/landing/LandingFooter.vue"
import UnderMaintenanceModal from "@/components/newModals/UnderMaintenanceModal.vue"

export default defineComponent({
  name: 'landing-layout',
  data: () => ({
    isOpen: false,
    showDropdown: false, 
    showCoinsDropdown: false
  }),
  components: {
    LandingSidebar, LandingNavbar, LangsDropdown, CoinsDropdown, LandingFooter, UnderMaintenanceModal
  },
  computed: {
    dropdownPosition() {
      const element = document.getElementById('dropdown-trigger')
      if (element) {
        const xPos = element.getBoundingClientRect().x
        const yPos = 64
        // // console.log(xPos, yPos)
        return {
          x: xPos,
          y: yPos
        }
      } else {
        return {x:0, y:0}
      }
    },
     coinsDropdownPosition() {
      const element = document.getElementById('coins-dropdown-trigger')
      if (element) {
        const xPos = element.getBoundingClientRect().x
        const yPos = 64
        // // console.log(xPos, yPos)
        return {
          x: xPos,
          y: yPos
        }
      } else {
        return {x:0, y:0}
      }
    },
    error() {
      // return this.$store.getters.error
      return null;
    },
    locale() {
      // return this.$store.getters.info.locale
      return "en-US";
    },
  },
  methods: {
    closeSidebar() {
      this.isOpen = false
    }, 
    triggerDropdown() {
      this.showDropdown = !this.showDropdown
    },
    triggerCoinsDropdown() {
      this.showCoinsDropdown = !this.showCoinsDropdown
    },
    closeCoinsDropdown() {
      this.showCoinsDropdown = false
    },
    closeDropdown() {
      this.showDropdown = false
    }
  },
  mounted() {
  },
});
</script>

