  .app-content {
    margin-left: 400px;
  }

  .app-content.full {
    margin-left: 0;
  }

  .sidebar,  .sidebar.open  {
      width:400px;
      -webkit-transform:translateX(0);
      transform:translateX(0);
  }

  .cross {
    margin-left: 0;
    visibility: hidden;
    width: 0;
    height: 0;
  }

  .link-to-sidebar {
    visibility: hidden;
  }
}

@media(max-width: 1100px) {
  .app-content {
    padding-left:0;
  }

  .sidebar{
      width:100%;
      -webkit-transform:translateX(-100%);
      transform:translateX(-100%);
  }

  .sidebar.open {
    -webkit-transform:translateX(0);
    transform:translateX(0);
