<template>
  <nav class="navbar" @click.prevent="$emit('click')">
      <div class="nav-wrapper">
        <div class="navbar-left">
            <a href="#" class="link-to-sidebar" @click.prevent="$emit('click')">
            <img class="burger" src="@/assets/newImages/burger.png">
            </a>

          <div class="navbar-logo">
             <img class="logo-image" src="@/assets/newImages/logo.png" @click="openHomePage()">
          </div>
        </div>
      <div class="navbar-right">
        <button class="basic-button" @click.stop="register">
                {{ $localizeFilter('registration') }}
        </button>
        <button class="basic-button" style="margin-right: 12px;" @click.stop="login">
                {{ $localizeFilter('login')  }}
        </button>

        <LangsDropdownTrigger style="margin-right: 20px;" @triggerDropdown="triggerDropdown"/>

         <div class="navbar-links">
            <router-link
                v-for="link in links"
                :id="link.id || null"
                :key="link.url"
                tag="div"
                active-class="active"
                :to="link.url"
                @click="link.onClick"
                :exact="link.exact"
                @click.native="handleTap(link.url)"
            >
            <div class="navbar-link">
              <button class="transparent-button" :class="(link.selected == true) ? 'selected' : ''">{{link.title}}</button>
            </div>
         </router-link>
        </div>
      </div>
      </div>
    </nav>
</template>

<script>
import localizeFilter from "@/filters/localize.filter";
import LangsDropdownTrigger from "@/components/app/langs/LangsDropdownTrigger.vue"

export default {
  components: {
    LangsDropdownTrigger
  },
  props: {
      coinsDropdownClosed: {
          default: false
      }
  },
  watch: {
    $route(to, from) {
      // // console.log(to)
      setTimeout(() => {
        if (to.fullPath) {
          this.$nextTick(()=>{ this.scrollFix(to.fullPath) })
        }
      },0)
    },
    coinsDropdownClosed(newVal, oldVal) {
      if (newVal==true) {
        this.$nextTick(this.deselectCoinsLink())
      }
    }
  },
  data: () => ({
    links: [
        { name: 'home', title: localizeFilter("home"), url: "/#home", exact: true, selected: false  }, 
        // { name: 'news', title: localizeFilter("news"), url: "/news", selected: false },
        { name: 'downloadApp', title: localizeFilter("download_app"), url: '#', selected: false, onClick() {window.open('https://cwcto.github.io/CapitalWhale.apk', '_blank')} },
        { name: 'liquidityProviding', title: localizeFilter("landing_liquidity_providing_title_2"), url: "/liquidity-providing", selected: false },
        { name: 'articlesAboutUs', title: localizeFilter("articles_about_us_2"), url: "/articlesAboutUs", selected: false },
        { name: 'rate', title: localizeFilter("rate"), url: "/rate", selected: false },
        { name: 'coins', title: localizeFilter("coins"), url: "#", id: 'coins-dropdown-trigger', selected: false },
        // { title: "", url: "/plans" },
        // { title: localizeFilter('marketing'), url: "/marketing" },
        // { title: "", url: "#" },
        { name: 'aboutUs', title: localizeFilter('about_us'), url: "/#aboutUs", selected: false},
        { name: 'contacts', title: localizeFilter('contacts'), url: "/#contacts", selected: false },
      ],
  }),
  methods: {
    openHomePage() {
      this.$router.push('/')
    },
    handleTap: function(hashbang) {
      if (hashbang == "#") {
        this.triggerCoinsDropdown()
      } else {
        this.scrollFix(hashbang)
      }
    },
    scrollFix: function(hashbang) {
      // console.log(hashbang)
        const idx = hashbang.lastIndexOf('#')
        const name = hashbang.slice(idx + 1);
        if (idx === -1 || name === '' || name.includes('/')) {
          window.scrollTo({top: 0, behavior: 'auto'})
          return
        }
        const element = document.getElementById(name)
        if (element) {
          const y = element.getBoundingClientRect().top + window.pageYOffset;
          window.scrollTo({top: y-50, behavior: 'auto'});
        }
    },
    register() {
      this.$router.push('/register')
    },
    login() {
      this.$router.push('/login')
    },
    triggerDropdown() {
       this.$emit('triggerDropdown')
    },
    triggerCoinsDropdown() {
      this.$emit('triggerCoinsDropdown')
      this.links.forEach(element => {
          if (element.name == 'coins') {
            element.selected = true
          }
      });
    },
    deselectCoinsLink() {
         this.links.forEach(element => {
          if (element.name == 'coins') {
            element.selected = false
          }
      });
    }
    // handleScroll (event) {
    //   // console.log(window.pageYOffset)
    // }
  },
  computed: {
  },
  mounted() { 
    // window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    // window.removeEventListener('scroll', this.handleScroll);
  },
}
</script>

