import { api } from '@/plugins/api'
import localizeFilter from '@/filters/localize.filter'
import { wsApi } from '../plugins/wsApi'



export default {
    state: () => ({
        walletData: null,
        walletCoins: [],
        walletMixinCoins: [],
        fillWalletNetwork: null, 
        fillWalletAddress: null,
        withdrawFromWalletNetwork: null,
        coinNetworks:[
            {
                networkTicker: "TRON",
                networkName: "Tron",
                networkFullName:"Tron(TRC-20)",
                minLength: 34,
                maxLength: 34,
                validatorParam:"trx"
            },
            {
                networkTicker: "BTC",
                networkName: "Bitcoin",
                networkFullName:"Bitcoin",
                minLength: 27,
                maxLength: 42,
                validatorParam:"bitcoin"
            },
            {
                networkTicker: "ETH",
                networkName: "Ethereum",
                networkFullName:"Ethereum(ERC-20)",
                minLength: 42,
                maxLength: 42,
                validatorParam:"ethereum"
            },
            {
                networkTicker: "BSC",
                networkName: "Binance Smart Chain",
                networkFullName:"Binance Smart Chain (BEP-20)",
                minLength: 42,
                maxLength: 42,
                validatorParam:"ethereum"
            },
        ]
    }),
    getters: {
        walletData: state => state.walletData,
        walletCoins: state => state.walletCoins,
        walletCoinByTicker: (state, getters) => (coinTicker) => {
          return getters.walletCoins.filter(coin => {
            return (coin.coinTicker === coinTicker)
          })[0]
        },
        walletMixinCoins: state => state.walletMixinCoins,
        networksForCoin: (state, getters) => (coinTicker) => {
            let result = []
            const mixinCoins = JSON.parse(JSON.stringify(getters.walletMixinCoins))
            Object.entries(mixinCoins).forEach(entry => {
                const [key, value] = entry
                if (key === coinTicker) {
                    result = value.networks
                }
            })
            return result
          },
          fillWalletNetwork: state => state.fillWalletNetwork,
          withdrawFromWalletNetwork: state => state.withdrawFromWalletNetwork,
          fillWalletAddress: state => state.fillWalletAddress,
          coinNetworks: state => state.coinNetworks,
          networkByTicker: (state, getters) => (networkTicker) => {
            return getters.coinNetworks.filter(network => {
            //   console.log(network.networkTicker, networkTicker, network.networkTicker === networkTicker)
              return (network.networkTicker === networkTicker)
            })[0]
          },
    },
    actions: {
        async getWalletData({ dispatch, commit }) {
            // const data = {
            //     coinTicker: 'CUTV',
            //     amount: 1000000,
            //     amountUSD: 45232399.65,
            //     rateChange: 0.04,
            // }
            let {wallets} = await api.getWalletData({})

            if(wallets['CUTV']) {
                wallets['CUTV'].rateCUT = wallets['CUTV']?.assetDecimals ? BigInt(wallets['CUTV'].assetDecimals) : null;
            }
            // wallets['USDV'].rateUSD = BigInt(wallets['USDV'].assetDecimals);
            // if(wallets['CUTV']) {
            //     wallets['USDV'].rateCUT = wallets['CUTV'].rateUSD ? BigInt(wallets['USDV'].assetDecimals) * BigInt(wallets['CUTV'].assetDecimals) / BigInt(wallets['CUTV'].rateUSD) : wallets['CUTV'].rateUSD;
            // }
            wallets['USDT'].rateUSD = BigInt(wallets['USDT'].assetDecimals);
            

            const mixinCoins = {
                // 'GDCV': { coinName: 'GadgetCoin Virtual', rateChange: -0, networks: ['ETH', 'TRON', 'BSC', 'BTC']},
                // 'CWPTV': { coinName: 'CWPT Virtual', rateChange: -0, networks: ['ETH', 'TRON', 'BSC', 'BTC']},
                // 'ITCV': { coinName: 'InternetCoin Virtual', rateChange: -0, networks: ['ETH', 'TRON', 'BSC', 'BTC']},
                // 'CUTV': { coinName: 'CuprumToken Virtual', rateChange: -0, networks: ['ETH', 'TRON', 'BSC', 'BTC']},
                
                // 'USDV': { coinName: 'USDT Virtual', rateChange: -0.0665, networks: ['TRON']},
                // 'BTCV': { coinName: 'Bitcoin Virtual', rateChange: -0.0665, networks: ['BTC']},
                // 'TRXV': { coinName: 'Tron Virtual', rateChange: -0.0665, networks: ['TRON']},

                'USDT': { coinName: 'Tether', rateChange: -0.0665, networks: ['TRON']},
                'BTC': { coinName: 'Bitcoin', rateChange: -0.0665, networks: ['BTC']},
                'TRX': { coinName: 'Tron', rateChange: -0.0665, networks: ['TRON']},
                'CWPT': { coinName: 'CapitalWhalePresaleToken', rateChange: -0.0665, networks: ['TRON']},
            }

            // let decimalsUSD = BigInt(wallets['USDV'].assetDecimals);
            let decimalsUSD = BigInt(wallets['USDT'].assetDecimals);
            let decimalsCUT = wallets['CUTV'] ? BigInt(wallets['CUTV'].assetDecimals) : null;

            
        
            wallets = Object.entries(wallets).filter(([currency])=>currency!=='BTC').map(([currency, {balance, assetDecimals, rateUSD, rateCUT}]) => {
                balance = balance ? BigInt(balance) : 0n
                assetDecimals = assetDecimals ? BigInt(assetDecimals) : 0n
                return {
                    coinTicker: currency,
                    amount: balance,
                    decimals: assetDecimals,
                    amountUSD: rateUSD ? balance * BigInt(rateUSD) / assetDecimals : 0n,
                    decimalsUSD,
                    amountCUT: rateCUT ? balance * BigInt(rateCUT) / assetDecimals : 0n,
                    ...(mixinCoins[currency] || {})
                }
            });

            let {amountUSD, amount} = wallets.reduce((acc, {amountUSD, amountCUT}) => {
                return {
                    amountUSD: acc.amountUSD + amountUSD,
                    amount: acc.amount + amountCUT
                }
            }, {amountUSD: 0n, amount: 0n});




            // console.log(wallets)
        
            // let walletData = wallets.reduce((acc, {coinTicker, amount, decimals, amountUSD, amountCUT}) => {
            //     acc[coinTicker] = {
            //     amountUSD: amountUSD,
            //     amountCUT: amountCUT,
            //     };
            //     return acc;
            // }, {amountUSD: 0n, amountCUT: 0n});
            const walletData = {
                coinTicker: 'CUTV',
                amount,
                amountUSD,
                decimalsUSD,
                decimals: decimalsCUT,
                rateChange: 0.04,
            }

            // console.log(wallets)
            // console.log(walletData)

            commit('SET_WALLET_DATA', walletData)
            commit('SET_WALLET_COINS', wallets)
            commit('SET_WALLET_MIXIN_COINS', mixinCoins)
        },
        // async getWalletCoins({ dispatch, commit }) {
        //     const {coins} = await api.getWalletCoins({})
            


        //     // const coins = [
        //     //     {coinTicker: 'GDCV', coinName: 'GadgetCoin Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
        //     //     {coinTicker: 'CWPTV', coinName: 'CWPT Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
        //     //     {coinTicker: 'ITCV', coinName: 'InternetCoin Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
        //     //     {coinTicker: 'CUTV', coinName: 'CuprumToken Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
        //     //     // {coinTicker: 'BTCV', coinName: 'Bitcoin Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
        //     //     // {coinTicker: 'ETHV', coinName: 'Ethereum Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
        //     //     {coinTicker: 'USDV', coinName: 'USDT Virtual', amount: 0.00, amountUSD: 0, rateChange: -0.0665},
        //     //   ]
        //     // console.log(coins)
        //     commit('SET_WALLET_COINS', coins)
        // },
        async getWalletCoin({ dispatch, commit }, { coinTicker }) {
            const coin = await api.getWalletCoin({ coinTicker })
            // const coin = {coinTicker: coinTicker, coinName: '', amount: 0.00, amountUSD: 0, rateChange: -0.0665}
            commit('SET_WALLET_COIN', {coin})
        },
        async setFillWalletNetwork({ dispatch, commit }, { network }) {
            commit('SET_FILL_WALLET_NETWORK', {network})
        },

        async setWithdrawFromWalletNetwork({ dispatch, commit }, { network }) {
            commit('SET_WITHDRAW_FROM_WALLET_NETWORK', {network})
        },

        async getFillWalletAdddress({ dispatch, commit, getters }, { coinTicker, network }) {
            const data = await api.getFillWalletAdddress({ coinTicker, network })
            commit('SET_FILL_WALLET_ADDRESS', {...data})
        }
    },
    mutations: {
        SET_WALLET_DATA(state, data) {
            state.walletData = data
        },
        SET_WALLET_COINS(state, data) {
            state.walletCoins = data
        },
        SET_WALLET_COIN(state, {coin}) {
            let idx = -1
            for (let i=0; i<state.walletCoins.length; i++) {
                if (state.walletCoins[i].coinTicker === coin.coinTicker) {
                    idx = i
                }
            }
            if (idx !== -1) {
                state.walletCoins[idx] = JSON.parse(JSON.stringify(coin))
            } else {
                state.walletCoins.push(JSON.parse(JSON.stringify(coin)))
            }
        },
        SET_WALLET_MIXIN_COINS(state, data) {
            state.walletMixinCoins = data
        },
        SET_FILL_WALLET_NETWORK(state, data) {
            state.fillWalletNetwork = data.network
        },
        SET_WITHDRAW_FROM_WALLET_NETWORK(state, data) {
            state.withdrawFromWalletNetwork = data.network
        },
        SET_FILL_WALLET_ADDRESS(state, data) {
            state.fillWalletAddress = data
        },
        
    }
}